.InputText
    font-family: "Inter", sans-serif
    font-size: 16px
    padding: 15px
    border-width: 1px
    border-style: solid
    border-radius: 16px
    outline: none
    box-sizing: border-box

@media all and (max-width: 380px)
    .InputText
        font-size: 14px
        padding: 14px
